.setting__main{
    display: flex;
 
}

.setting__container{
    border: 2px solid green;
    max-height: 100vh;
    overflow: hidden;
    width: 100%;
    background-color: #F5F5F5;
    
}
.setting_container_main{
    background-color: white;
    width: 80%;
    margin: 30px 50px;
    max-height: 100vh;
    
}

.setting_container__both_container{
    width: 95%;
    max-height: 100vh;
    overflow: hidden;
    margin: auto;
    margin-top: 30px;
    display: flex;
    justify-content: space-between;
}

.setting_4big_container{
    padding: 10px;

}

.setting_1big_container{
    box-shadow: rgba(17, 17, 26, 0.05) 0px 1px 0px, rgba(17, 17, 26, 0.1) 0px 0px 8px;
    width: 50%;
    padding: 10px 10px;
    border-radius: 20px;
    margin-top: 10px;
}

